body {
  position: relative;
  width: 100%;
  height: auto;
  color: #FFF;
  font-family: 'Poppins', sans-serif;
  margin: auto; }
  @media only screen and (max-width: 992px) {
    body {
      width: 100%; } }
  @media only screen and (min-width: 1270px) {
    body {
      width: 1440px;
      height: 1089.5px; } }
  body aside {
    position: relative;
    background: #070D59;
    float: left; }
    @media only screen and (max-width: 992px) {
      body aside {
        width: 30%;
        height: 750px; } }
    @media only screen and (max-width: 767px) {
      body aside {
        width: 100%;
        height: auto; } }
    @media only screen and (min-width: 1270px) {
      body aside {
        width: 384px;
        height: 1090px;
        left: 0px;
        top: 0px;
        padding-top: 70px; } }
  body .rightSide {
    float: left; }
    @media only screen and (max-width: 992px) {
      body .rightSide {
        width: 70%;
        margin-top: 30px; } }
    @media only screen and (max-width: 767px) {
      body .rightSide {
        width: 100%; } }
    @media only screen and (min-width: 1270px) {
      body .rightSide {
        width: 992px;
        padding-top: 60px;
        margin-left: 64px; } }
    body .rightSide h2.docTitle {
      color: #2D1F3F;
      font-weight: bold;
      font-size: 36px;
      line-height: 54px;
      margin-bottom: 50px; }
      @media only screen and (max-width: 992px) {
        body .rightSide h2.docTitle {
          padding-left: 15px; } }
      body .rightSide h2.docTitle:before {
        content: url(data:image/png;base64,);
        padding-right: 25px;
        position: relative;
        top: 12px; }

