.uploadBtn {
  position: absolute;
  bottom: 30px;
  margin-left: 15px; }
  @media only screen and (max-width: 992px) {
    .uploadBtn {
      width: 90%;
      margin: auto;
      left: 10px;
      bottom: 50px; } }
  @media only screen and (max-width: 767px) {
    .uploadBtn {
      display: none; } }
  .uploadBtn button {
    width: 305px;
    height: 70px;
    background: #F7B633;
    border: 1px solid #F7B633;
    border-style: solid;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    font-size: 22px;
    line-height: 33px;
    font-family: Poppins;
    font-style: normal;
    font-weight: 600;
    color: #FFF; }
    @media only screen and (max-width: 992px) {
      .uploadBtn button {
        width: 100%;
        height: 50px;
        font-size: 18px; } }
    .uploadBtn button:before {
      content: url(data:image/png;base64,);
      padding-right: 15px; }
      @media only screen and (max-width: 992px) {
        .uploadBtn button:before {
          padding-right: 10px;
          top: 4px;
          position: relative; } }
    .uploadBtn button input {
      position: absolute;
      left: 0px; }

