@import "variables";

body{
    position: relative;
    width: 100%;
    height: auto;
    color: $white;
    font-family: 'Poppins', sans-serif;
    margin: auto;
    @media #{$tablet}{
        width:100%;
    }
    @media #{$largeScreen} {
        width: 1440px;
        height: 1089.5px;
    }
    aside{
        position: relative;
        background: $blueBG;
        float: left;
        @media #{$tablet}{
            width: 30%;
            height: 750px;
        }
        @media #{$mobile}{
            width: 100%;
            height: auto;
        }
        @media #{$largeScreen} {
            width: 384px;
            height: 1090px;
            left: 0px;
            top: 0px;
            padding-top: 70px;
        }
        
    }
    .rightSide{
        float: left;
        @media #{$tablet}{
            width: 70%;
            margin-top: 30px;
        }
        @media #{$mobile}{
            width: 100%;
        }
        @media #{$largeScreen} {
            width: 992px;
            padding-top: 60px;
            margin-left: 64px;
        }
        h2.docTitle{
            @media #{$tablet}{
                padding-left: 15px;
            }
            color: $HeadingText;
            font-weight: bold;
            font-size: 36px;
            line-height: 54px;
            margin-bottom: 50px;
            &:before{
                content: url("../Assets/Group-large.png");
                padding-right: 25px;
                position: relative;
                top: 12px;
            }
        }
    }
}